import { Address, AddressType } from '../models/types';

export const toCamelCase = (str = '') => {
  return str.substr(0, 1).toLowerCase() + str.substr(1);
};

const addressFrom = (address: any, type: AddressType): Address => ({
  addressLine1: address[toCamelCase(`${type}AddressLine1`)],
  addressLine2: address[toCamelCase(`${type}AddressLine2`)],
  streetName: address[toCamelCase(`${type}StreetName`)],
  postCode: address[toCamelCase(`${type}PostCode`)],
  town: address[toCamelCase(`${type}Town`)],
  countrySubDivision: address[toCamelCase(`${type}CountrySubDivision`)],
  country: address[toCamelCase(`${type}Country`)],
});

export const addressFromContact = (address: any): Address => addressFrom(address, AddressType.CONTACT);

export const addressFromBank = (address: any): Address => addressFrom(address, AddressType.BANK);

export const addressFromAccountHolder = (address: any): Address => addressFrom(address, AddressType.ACCOUNTHOLDER);

export const isObjectTruthy = <T, K extends keyof T>(obj: T): boolean => {
  return Object.keys(obj).some((key) => !!obj[key as K]);
};

export const addressToString = (address: Address) =>
  [
    address[`addressLine1`],
    address[`addressLine2`],
    address[`streetName`],
    address[`postCode`],
    address[`town`],
    address[`countrySubDivision`],
  ]
    .filter(Boolean)
    .join(', ');
