import { Provider } from '@angular/core';
import { ContactHttpService } from './contact-http.service';
import { ContactService } from './contact.service';
import { ContactCountryListService } from './contact-country-list.service';

export const provideContactServices: Provider[] = [
  { provide: ContactHttpService, useClass: ContactHttpService },
  {
    provide: ContactService,
    useClass: ContactService,
    deps: [ContactHttpService],
  },
  ContactCountryListService,
];
