let countries: IBANCountryList = { ..._countries };

export interface Country {
  bban: string;
  example?: string;
  len: number;
  name: string;
}

export interface IBANCountryList {
  [countryCode: string]: Country;
}

export const addIBANCountries = (newCountries: IBANCountryList): IBANCountryList => {
  countries = { ...countries, ...newCountries };

  return countries;
};

/**
 * Given an iban returns that country specification
 *
 * type {function}
 * inner
 * throws {Error} If Country code is not defined
 *
 * @param {string} iban A valid IBAN number
 * @returns {object} Country specification
 */
export default (iban: string): Country => {
  const countryCode = iban.slice(0, 2);

  if (!{}.hasOwnProperty.call(countries, countryCode)) {
    throw new Error(`Country code ${countryCode} is not supported`);
  }

  return countries[countryCode];
};
