import { InputName, InputGroupName, ContactItem, ApprovalDetails } from '../models/types';
import { addressFromContact } from './address';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { throwError } from 'rxjs';
import { ContactApprovalStatusMap } from '../constants';

export const filterEmptyValues = (obj: { [key: string]: any }) =>
  Object.keys(obj).reduce((result, key) => ({ ...result, ...(obj[key] ? { [key]: obj[key] } : {}) }), {});

export const toInteger = (value: any): number => parseInt(value, 10) || 0;

export const toBoolean = (value: string | boolean | undefined): boolean => {
  if (typeof value === 'boolean') {
    return value;
  }

  return value === 'true';
};

export const sanitizeObject = <T>(obj: T): Object => {
  const sanitizedObj: { [key: string]: any } = {};
  for (const key in obj) {
    if ((obj as Object).hasOwnProperty(key) && obj[key] && key !== InputName.COPY_CONTACT_ADDRESS) {
      sanitizedObj[key] = obj[key];
    }
  }

  return sanitizedObj;
};

export const flattenObject = <T>(obj: T): Object => {
  let flattenedObj: { [key: string]: any } = {};
  for (const key in obj) {
    if ((obj as Object).hasOwnProperty(key) && obj[key]) {
      if (typeof obj[key] === 'object') {
        flattenedObj = { ...flattenedObj, ...sanitizeObject(obj[key]) };
      } else {
        flattenedObj[key] = obj[key];
      }
    }
  }

  return flattenedObj;
};

export const fromContactFormToContactModel = <T>(formObj: T) => {
  let model: any = {};
  model['accounts'] = [];

  for (const key in formObj) {
    if ((formObj as Object).hasOwnProperty(key)) {
      if (key === InputGroupName.CONTACT) {
        model = { ...model, ...flattenObject(formObj[key]) };
      } else if (key.indexOf(InputGroupName.ACCOUNT) > -1) {
        model.accounts.push(flattenObject(formObj[key]));
      }
    }
  }

  return model;
};

export const fromContactItemToContactModel = (contact: ContactItem): { [key: string]: any } => {
  const keys = ['name', 'alias', 'phoneNumber', 'emailId', 'contactPerson', 'additions'];

  return {
    ...keys.reduce((details, key) => {
      const value = (contact as { [key: string]: any })[key];
      if (value) {
        return { ...details, [key]: value };
      }

      return details;
    }, {}),
    ...filterEmptyValues(addressFromContact(contact)),
    accounts: contact.accounts.map(filterEmptyValues),
  };
};

export const fromResponseBodyList = <T>(response: HttpResponse<T>) => ({
  totalCount: parseInt(response.headers.get('x-total-count') as string, 10) || 0,
  items: response.body || [],
});

export const fromErrorResponse = (error: HttpErrorResponse) =>
  throwError({ status: error.status, message: error.message });

export const getApprovalStatus = (approval: ApprovalDetails): string => {
  let label = '';
  const approvalDetailsStatus: any = approval.approvalDetails && approval.approvalDetails.status;
  const currentStatus = ContactApprovalStatusMap[approvalDetailsStatus];

  if (
    approval.approvalDetails &&
    currentStatus === ContactApprovalStatusMap.PENDING &&
    approval.approvalDetails.policy &&
    approval.approvalDetails.policy.items &&
    approval.approvalDetails.records
  ) {
    const approvals = approval.approvalDetails.records.length;
    const total = approval.approvalDetails.policy.items.reduce(
      (totalCount, policy) => totalCount + policy.numberOfApprovals || 0,
      0,
    );
    label = ` (${approvals}/${total})`;
  }

  return `${currentStatus}${label}`;
};
