import { Injectable, OnDestroy } from '@angular/core';
import { ContactHttpService } from './contact-http.service';
import { BehaviorSubject, merge, Observable, of, ReplaySubject, Subject, Subscription } from 'rxjs';
import { filter, finalize, map, mapTo, mergeMap, scan, shareReplay, switchMap, takeUntil } from 'rxjs/operators';
import {
  ApprovalRequired,
  ContactActions,
  ContactItem,
  ContactRequestParameters,
  ContactResponse,
  ContactState,
  ExtendedContactResponse,
  LoadMoreMethod,
} from '../models/types';
import { SortEvent } from '@backbase/ui-ang/table';
import { SortDirection, ApprovalSort } from '@backbase/data-ang/contact-manager';
export interface ContactsServiceConfig {
  pageSize: number;
}

export const ContactsServiceConfigDefaults = {
  pageSize: 50,
};

/**
 * Service calls the APIs for all the actions done by the user.
 * This service Injects ContactHttpService
 *
 * @see ContactHttpService
 * @usageNotes
 *
 *
 * ```ts
 * @NgModule({
 *   ...
 *   providers: [
 *     ...
 *     ContactService,
 *   ],
 * })
 * export class MyContactCommonAngModule {}
 * ```
 *
 * ### Inject this service into your component
 *
 * ```ts
 * @Component({
 *   ...
 *   providers: [ContactService],
 * })
 *  export class ContactCommonAngComponent {
 * ```
 */
@Injectable()
export class ContactService implements OnDestroy {
  protected readonly destroy$ = new Subject();
  private readonly changePage$ = new Subject<ContactRequestParameters['from']>();
  private readonly loadMore$ = new Subject<ContactRequestParameters['from']>();
  private readonly load$ = new Subject();
  private readonly refresh$ = new Subject<ContactState['loadMoreMethod']>();
  private readonly search$ = new Subject<{
    query: ContactRequestParameters['query'];
    from: ContactRequestParameters['from'];
  }>();

  private readonly sort$ = new Subject<{
    sort: ContactRequestParameters['sort'];
    direction: ContactRequestParameters['direction'];
  }>();

  protected readonly config$ = new BehaviorSubject<ContactsServiceConfig>(ContactsServiceConfigDefaults);

  private readonly id$$ = new ReplaySubject<string | null>(1);
  private readonly subscriptions: Subscription[] = [];
  private readonly contact$ = this.id$$.pipe(
    switchMap((id) => {
      if (!id) {
        return of(undefined);
      }

      return this.contactHttpService.getContact(id);
    }),
  );

  readonly loading$$ = new ReplaySubject<boolean>(1);
  readonly loading$ = this.loading$$.asObservable();

  protected set loading(val: boolean) {
    this.loading$$.next(val);
  }

  private readonly acting$$ = new ReplaySubject<boolean>(1);
  readonly acting$ = this.acting$$.asObservable();

  protected set acting(val: boolean) {
    this.acting$$.next(val);
  }

  private readonly pageSize$: Observable<number> = this.config$.pipe(map((config) => config.pageSize));

  private readonly actions$ = merge(
    this.load$.pipe(mapTo({ action: ContactActions.Load })),
    this.search$.pipe(map(({ query, from }) => ({ action: ContactActions.Search, query, from }))),
    this.refresh$.pipe(map((loadMoreMethod) => ({ action: ContactActions.Refresh, loadMoreMethod }))),
    this.changePage$.pipe(map((from) => ({ action: ContactActions.ChangePage, from }))),
    this.loadMore$.pipe(map((from) => ({ action: ContactActions.LoadMore, from }))),
    this.pageSize$.pipe(map((pageSize) => ({ pageSize }))),
    this.sort$.pipe(map(({ sort, direction }) => ({ action: ContactActions.Sort, sort, direction }))),
  );

  protected readonly state$ = this.actions$.pipe(
    scan((state, action) => ({ ...state, ...action })),
    map(({ pageSize, loadMoreMethod, query, ...state }: ContactState) => {
      const isRefresh = state.action === ContactActions.Refresh;
      const size = pageSize || ContactsServiceConfigDefaults.pageSize;
      const from = state.from || 0;

      return {
        ...state,
        query: query || '',
        size: isRefresh && loadMoreMethod === LoadMoreMethod.LOAD_MORE ? size * (from + 1) : size,
        from: isRefresh && loadMoreMethod === LoadMoreMethod.LOAD_MORE ? 0 : from,
      };
    }),
    filter((state) => state.action !== undefined),
    shareReplay(1),
  );

  protected readonly effects$: Observable<ExtendedContactResponse> = this.state$.pipe(
    mergeMap(({ action, ...parameters }) => {
      this.loading = true;
      const isLoadMore = action === ContactActions.LoadMore;

      return this.contactHttpService.getContacts(this.removeEmptyParameters(parameters)).pipe(
        map((response) => ({ ...response, isLoadMore })),
        finalize(() => (this.loading = false)),
      );
    }),
    takeUntil(this.destroy$),
    shareReplay(1),
  );

  protected readonly contactsSource$ = new BehaviorSubject<ContactResponse | undefined>(undefined);

  public get contacts$() {
    return this.contactsSource$.asObservable();
  }

  constructor(private readonly contactHttpService: ContactHttpService) {}

  protected removeEmptyParameters<T>(parameters: T): T {
    return Object.entries(parameters)
      .filter(([key, value]) => value !== '')
      .reduce((prev, curr) => ({ ...prev, [curr[0]]: curr[1] }), {}) as T;
  }

  /**
   * Sets the Subject with updated contactService config.
   *
   * @param {ContactsServiceConfig} config - new config for Contact Service
   */
  init(config: ContactsServiceConfig) {
    this.config$.next(config);
  }

  /**
   *  Updates the subject with value
   */
  load() {
    this.load$.next();
  }

  /**
   * Updates the loadMore Subject with pageFrom value.
   *
   * @param {number} from - page From the data has to be fetched.
   */
  loadMore(from: number) {
    this.loadMore$.next(from);
  }

  /**
   * Updates the changePage subject to updated page.
   *
   * @param {number} from - page From the data has to be fetched.
   */
  changePage(from: number) {
    this.changePage$.next(from);
  }

  /**
   * Triggers the refresh listener with current load more.
   *
   * @param {LoadMoreMethod} loadMoreMethod - Current LoadMore method can be pagination / load-more
   */
  refresh(loadMoreMethod: LoadMoreMethod) {
    this.refresh$.next(loadMoreMethod);
  }

  /**
   * Triggers search Listner with search query.
   *
   * @param {string} query - search text entered by the user.
   */
  searchContacts(query: string) {
    this.search$.next({ query, from: 0 });
  }

  /**
   * Triggers sort Listner with mapped event.
   *
   * @param {string} sortEvent - table sort event.
   */
  sortApprovals(sortEvent: SortEvent) {
    const castedDirection: SortDirection = sortEvent.direction.toUpperCase() as SortDirection;
    const mappedEvent = { sort: sortEvent.column as ApprovalSort, direction: castedDirection };
    this.sort$.next(mappedEvent);
  }

  /**
   * Subscribes to the contact of a given contact ID.
   *
   * @param {Observable<string | null>} id Observable of Contact ID.
   * @returns {Observable<ContactItem | undefined>} of contact Details with that ID.
   */
  getContactFrom(id: Observable<string | null>): Observable<ContactItem | undefined> {
    this.subscriptions.push(id.subscribe(this.id$$));

    return this.contact$;
  }

  /**
   * Returns the details for a given ID.
   *
   * @param {string} id contact ID
   * @returns {Observable<ContactItem | undefined>} Observable of contact Details for given ID.
   */
  getContactById(id: string): Observable<ContactItem | undefined> {
    return this.contactHttpService.getContact(id);
  }

  /**
   * Calls the delete API for specific ID
   *
   * @param {string} contactId Contact ID
   * @returns {Observable<ApprovalRequired>} observable of a result
   */
  deleteContact(contactId: string): Observable<ApprovalRequired> {
    this.acting = true;

    return this.contactHttpService.deleteContact(contactId).pipe(finalize(() => (this.acting = false)));
  }

  /**
   *
   * @param {ContactItem} contact contact item object
   * @returns {Observable<ApprovalRequired>} response of a contact update
   */
  updateContact(contact: ContactItem): Observable<ApprovalRequired> {
    this.acting = true;

    return this.contactHttpService.updateContact(contact).pipe(finalize(() => (this.acting = false)));
  }

  /**
   * Calls the API for approval
   *
   * @param {string} approvalId - approval ID
   * @param {string?} comment explanation of request approval
   * @returns {Observable<string>} response of a contact edit approval
   */
  approveContact(approvalId: string, comment?: string): Observable<string> {
    this.acting = true;

    return this.contactHttpService.approveContact(approvalId, comment).pipe(finalize(() => (this.acting = false)));
  }

  /**
   * Calls the API for rejction
   *
   * @param {string} approvalId approval ID
   * @param {string?} comment explanation of request rejection
   * @returns {Observable<string>} response of an approval rejection
   */
  rejectContact(approvalId: string, comment?: string): Observable<string> {
    this.acting = true;

    return this.contactHttpService.rejectContact(approvalId, comment).pipe(finalize(() => (this.acting = false)));
  }

  ngOnDestroy() {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
    this.destroy$.complete();
    this.loadMore$.complete();
    this.changePage$.complete();
    this.refresh$.complete();
  }
}
